import Vue from 'vue';
import VueRouter from 'vue-router';
import Main from "@/components/Main";
import FileSystem from "@/components/FileSystem";
import LoadFile from "@/components/LoadFile";

Vue.use(VueRouter);

const routes = [
    {
        path: '/files',
        component: FileSystem,
        props: true
    },
    {
        path: '/a/:userID/:id',
        props: true,
        component: LoadFile
    },
    {
        path: '*',
        component: Main,
        props: true
    }
];

export default new VueRouter({
    mode: 'history',
    routes
});