<template>
  <main>
    <div class="container block">
      <div class="files-container">
        <div class="files" v-if="files.length > 0">
          <div :id="i.id" @contextmenu="onContextMenu" :key="i.id" v-for="i in files">
            <div class="icon"></div>
            <div class="name">{{i.name}}</div>
            <div :style="stylesFromActiveEl" class="active" v-if="activeIDFile === i.id" >
              <div class="dropdown-item pointer" @click="() => moreDetails(i.id)">Подробнее</div>
              <div class="dropdown-item pointer" @click="() => deleteFile(i.id)">Удалить</div>
            </div>
          </div>
        </div>
        <h2 v-else-if="files.length === 0 && !loaded" :style="{
          textAlign: 'center',
          display: 'grid',
          alignItems: 'center',
          height: '100%'
        }">Загрузите свой первый файл!</h2>
        <h2 v-else :style="{
          textAlign: 'center',
          display: 'grid',
          alignItems: 'center',
          height: '100%'
        }">Loading...</h2>
      </div>
      <div class="d-flex justify-content-center">
        <router-link to="/"><button class="btn btn-danger exit">Назад</button></router-link>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  name: "FileSystem",
  props: ['setAID', 'urlBackend', 'auth', 'out', 'setFiles', 'checkAuth', 'setOldFileInfo', 'setLoad', 'files', 'AID', 'setEventListener', 'removeEventListener', 'addFile', 'deleteFile'],
  data(){
    return {
      activeIDFile: null,
      stylesFromActiveEl: {
        top: '0px',
        left: '0px',
      },
      loaded: false,
    }
  },
  async mounted() {
    if(!this.AID && !this.auth){
      return this.$router.history.push('/');
    }
    if(!this.files.length){
      this.loaded = true;
      let res = await fetch(`${this.urlBackend}/api/getMyFiles`, {
        credentials: 'include'
      })
      let json = await res.json();
      this.loaded = false;
      if(!json.data.auth){
        this.$router.history.push('/');
      }
      this.setFiles(json.data.files);
    }
  },
  methods: {
    onContextMenu(e){
      e.preventDefault();
      let file = e.target.parentElement;
      let id = file.getAttribute('id');
      let cb = () => {
        this.activeIDFile = null;
        this.removeEventListener();
      };
      this.setEventListener(cb);
      this.activeIDFile = id;
      this.stylesFromActiveEl.top = `${e.y}px`;
      this.stylesFromActiveEl.left = `${e.x}px`;
    },
    moreDetails(id){
      let file = this.files.find(file => file.id === id);
      if(!file){
        return this.$swal('Ошибка', 'Ошибка на стороне клиента. Пожалуйста перезагрузите страницу.', 'error')
      }
      this.$swal({
        icon: 'success',
        html:
            `<ul>
    <li>${file.name}</li>
    <li>${file.size}MB</li>
    <li>${file.extname.slice(1)}</li>
    <li>${file.created}__CREATE</li>
    <li>${file.edited}__EDIT</li>
    <li><a href="${location.origin}/a/${this.AID}/${file.id}">${location.origin}/a/${this.AID}/${file.id}</a></li>
</ul>`,
        title: `Информация по файлу '${file.name}'`
      })
    },
  }
}
</script>

<style lang="sass" scoped>
.active
  position: absolute
  background: #fff
  border: 1px dashed #ccc
  padding: 5px
.files-container::-webkit-scrollbar
  width: 5px
  background: #ccc

.files-container::-webkit-scrollbar-button
  background-repeat: no-repeat
  width: 5px
  height: 0px

.files-container::-webkit-scrollbar-track
  background-color: #ecedee

.files-container::-webkit-scrollbar-thumb
  -webkit-border-radius: 0px
  border-radius: 0px
  background-color: #6dc0c8

.files-container::-webkit-scrollbar-thumb:hover
  background-color: #56999f

.files-container::-webkit-resizer
  background-repeat: no-repeat
  width: 4px
  height: 0px

.files-container::-webkit-scrollbar
  width: 4px

.exit
  width: 200px
  margin-top: 20px

.files-container
  width: 520px
  height: 500px
  padding: 20px
  overflow: scroll
  overflow-x: hidden
  .files
    display: grid
    grid-template-columns: repeat(3, 1fr)
    grid-row-gap: 20px

    > div
      margin: 0 auto
      .icon
        width: 100%
        height: 45px
        background: #ccc

</style>