<template>
  <div @click="eventListener" id="app">
    <router-view :urlBackend="urlBackend" :checkAuth="checkAuth" :out="out" :setFiles="setFiles" :deleteFile="deleteFile" :addFile="addFile" :removeEventListener="removeEventListener" :setAID="setAID" :setEventListener="setEventListener" :setOldFileInfo="setOldFileInfo" :setLoad="setLoad" :OldFileInfo="OldFileInfo" :load="load" :auth="auth" :files="files" :AID="AID" />
  </div>
</template>

<script>
import './assets/sass/main.sass';
export default {
  name: 'App',
  data(){
    return {
      OldFileInfo: null,
      load: false,
      files: [],
      AID: '',
      urlBackend: 'https://adisk-backend.herokuapp.com',
      eventListener(){},
      auth: false,
    }
  },
  async mounted() {
    await this.checkAuth();
  },
  methods: {
    async checkAuth(){
      if(this.$cookies.get('userID')){
        this.load = true;
        let res = await fetch(`http://localhost:3005/api/checkAuth`, {
          credentials: 'include'
        });
        let json = await res.json();
        if(json.data){
          // auth +
          this.auth = true;
          this.AID = json.data;
          this.load = false;
        } else {
          this.load = false;
        }
      }
    },
    out(){
      this.$cookies.remove('userID');
      this.auth = false;
      this.AID = '';
      this.files = [];
    },
    async deleteFile(id){
      await fetch(`http://localhost:3005/api/deleteFile?id=${id}`, {
        method: 'DELETE',
        credentials: 'include'
      });
      let res = await fetch(`http://localhost:3005/api/getMyFiles`, {
        credentials: 'include'
      });
      let json = await res.json();
      this.files = json.data.files;
    },
    setEventListener(cb){
      this.eventListener = cb;
    },
    removeEventListener(){
      this.eventListener = () => {};
    },
    setAID(val){
      this.AID = val;
    },
    setLoad(status){
      this.load = status;
    },
    addFile(file){
      this.files.push(file);
    },
    setFiles(files){
      this.files = files;
    },
    setOldFileInfo(info){
      this.OldFileInfo = {
        name: info.name,
        size: info.size,
        edited: info.edited,
        created: info.created,
        id: info.id,
        extname: info.extname,
        pathToFile: info.path,
      }
    }
  }
}
</script>

<style>
  .loader{
    text-align: center;
    margin-top: 10px;
  }
</style>
