<template>
  <div>
    <h1 v-if="timer !== 0">{{timer}}</h1>
    <h1 v-else-if="!fileIsset">Ожидайте...</h1>
    <a :href="`${this.urlBackend}/${this.pathToFile}`"><button class="btn btn-danger" v-if="fileIsset">Скачать файл</button></a>
  </div>
</template>

<script>
export default {
  name: "LoadFile",
  props: ['urlBackend', 'checkAuth', 'out', 'setFiles', 'deleteFile', 'addFile', 'removeEventListener', 'setAID', 'AID', 'auth', 'userID', 'id', 'setEventListener', 'files', 'setLoad', 'setOldFileInfo'],
  data(){
    return {
      timer: 5,
      checkedFileStatus: false,
      fileIsset: false,
      pathToFile: '',
    }
  },
  async mounted() {
    this.checkFile();
    let interval = setInterval(() => {
      if(this.timer === 0){
        this.action();
        return clearInterval(interval);
      }
      this.timer--;
    }, 1000)
  },
  methods: {
    action(){
      if(this.checkedFileStatus){
        this.fileIsset = true;
      } else {
        this.$router.push('/');
      }
    },
    async checkFile(){
      let { userID, id } = this.$route.params;
      let res = await fetch(`${this.urlBackend}/api/issetFile?userID=${userID}&fileID=${id}`);
      let json = await res.json();
      if(json.data){
        this.checkedFileStatus = true;
        this.pathToFile = json.data.path;
      }
    }
  }
}
</script>

