<template>
  <div v-if="!fileInfo"></div>
  <div v-else class="loaded-files loaded-files-active">
    <hr>
    <div class="file">
      <div class="file__name">
        {{fileInfo.name}}
      </div>
      <div class="file__content">
        <div class="file__content-title">Тип файла: <div class="file__content-value file-type">{{fileInfo.extname.slice(1)}}</div>
        </div>
        <div class="file__content-title">Размер файла: <div class="file__content-value file-size">{{fileInfo.size}}MB</div>
        </div>
        <div class="file__content-title">Дата изменения файла: <div class="file__content-value file-data-edited">
        {{fileInfo.edited}}</div>
        </div>
        <div class="file__content-title">Дата создания файла: <div class="file__content-value file-data-created">{{fileInfo.created}}
        </div>
        </div>
        <div class="file__content-title">Путь до файла: <div class="file__content-value path-to-file">
          {{getFullPathToFile(fileInfo.pathToFile)}}
        </div>
        </div>
        <div class="file__content-title">Ссылка на скачивание: <a class="file__content-value link-to-file"><a
            target="_blank"
            :href="getFullPathToFile(fileInfo.pathToFile)">{{getFullPathToFile(fileInfo.pathToFile)}}</a>
        </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Files",
  props: ['fileInfo', 'urlBackend', 'pathToFile', 'AID'],
  methods: {
    getFullPathToFile(){
      return `${location.origin}/a/${this.AID}/${this.fileInfo.id}`;
    }
  }
}
</script>

