<template>
  <div class="block">
    <h4>Войти под AID</h4>
    <form @submit="submit">
      <input v-model="inpValue" type="text" class="form-control auth-input" placeholder="Введите AID">
      <button class="btn btn-danger auth-btn">Войти</button>
    </form>
  </div>
</template>

<script>
export default {
  name: "AuthForm",
  props: ['setAID', 'urlBackend'],
  methods: {
    async submit(e){
      e.preventDefault();
      let val = this.inpValue;
      if(!val){
        return alert('Введите Ваш AID');
      }
      this.inpValue = '';
      const res = await fetch(`${this.urlBackend}/api/auth?AID=${val}`, {
        credentials: 'include'
      });
      const json = await res.json();
      if(json.data === true){
        this.setAID(val);
      } else {
        alert(json.msg);
      }
    }
  },
  data(){
    return {
      inpValue: '',
    }
  }
}
</script>

<style lang="sass">
  input
    border: 1px dashed #ccc !important
  .auth-btn
    width: 93px
  form
    display: flex
    justify-content: space-between
  .auth-input
    width: 78.5%
  .block
    margin-bottom: 20px
</style>